@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css?family=Titillium+Web');

html, body {
    height: 100%;
}
body {
    margin: 0;
    padding: 0;
    font-family: 'Titillium Web', sans-serif;
    background-image: -webkit-gradient(linear, left top, right top, from(#06beb6), to(#48b1bf));
    background-image: -webkit-linear-gradient(left, #06beb6, #48b1bf);
    background-image: -o-linear-gradient(left, #06beb6, #48b1bf);
    background-image: linear-gradient(to right, #06beb6, #48b1bf);
}
#root{
    padding: 0;
    height: 100%;
}



